body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#121616;height:auto}
.home-box{background:#192325;background:linear-gradient(to right, #0d1112 0%, #1d3130 45%, #136952 85%, #019664 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#0d1112", endColorstr="#198c71",GradientType=1 );height:100vh;display:flex;flex-direction:column;justify-content:space-between}.home-links-box{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;z-index:2}.particles{position:absolute;width:100%;height:100%}@media(min-width: 812px){.home-links-box{flex-direction:row}}
header{margin:0 auto;padding:10px 3vw;display:inline-flex;justify-content:space-between}header .logo-svg{width:40px;height:40px;opacity:.8}header .logo-svg .cls-1{fill:#10c286}header .logo-svg:hover{opacity:1}
.nav-box{width:80px;margin-left:40px;height:auto}.nav-svg{width:30px;height:30px;fill-opacity:.8;margin-left:35px;margin-top:5px}.nav-svg .cls-1{fill:#10c286}.nav-svg:hover{fill-opacity:1}.nav-list{display:flex;flex-direction:column;align-items:flex-end;width:100%}.nav-link{margin:10px;margin-left:auto}.nav-link a{font-family:"Rubik";font-weight:bold;text-transform:uppercase;-webkit-text-emphasis:none;text-emphasis:none;text-decoration:none;color:#18584a;padding:2px;border:none 2px #10c286}.nav-link a:hover{color:#10c286;border:solid;transition-duration:.2s;transition-timing-function:ease-out}
.home-link{display:flex;justify-content:center;align-items:center;width:110px;height:80px;font-size:150%;margin:0 20px}.home-link span{color:#10c286;height:100%}.home-link a{color:#18584a;text-decoration:none;font-family:"Rubik";font-weight:bold;height:100%;padding:2.5px 1px 0 1px;text-transform:uppercase}.home-link:hover span{color:#136952;transition-duration:.3s;transition-timing-function:ease-in-out}.home-link:hover a{color:#10c286;transition-duration:.3s;transition-timing-function:ease-in-out}@media(min-width: 812px){.home-link:hover a{border-color:#10c286;border-bottom:solid 3px;transition-duration:.2s;transition-timing-function:ease-in-out}}

